import { classes, Container } from '@adobe-commerce/elsie/lib';
import {
  PurchaseOrdersTable,
  PurchaseOrdersHeader,
  PurchaseOrdersTableActions,
} from '@/b2b-purchase-order/components';
import {
  usePurchaseOrders,
  useCustomerRolePermissions,
} from '@/b2b-purchase-order/hooks';
import { useText } from '@adobe-commerce/elsie/i18n';
import { defaultPageSizeConfig } from '@/b2b-purchase-order/configs';
import { EnumPurchaseOrdersView } from '@/b2b-purchase-order/types/api';
import { RequireApprovalPurchaseOrdersProps } from '@/b2b-purchase-order/types/containers';

export const RequireApprovalPurchaseOrders: Container<
  RequireApprovalPurchaseOrdersProps
> = ({
  initialPageSize = defaultPageSizeConfig,
  routePurchaseOrderDetails,
  setColumns,
  setRowsData,
  className = '',
  withHeader = true,
  withWrapper = true,
  skeletonRowCount = 5,
}) => {
  const view = EnumPurchaseOrdersView.CUSTOMER_APPROVAL_PURCHASE_ORDERS;

  const t = useText({
    headerText: 'PurchaseOrders.requireApprovalPurchaseOrders.containerTitle',
    actionView: 'PurchaseOrders.purchaseOrdersTable.actionView',
    poNumber: 'PurchaseOrders.purchaseOrdersTable.tableColumns.poNumber',
    orderNumber: 'PurchaseOrders.purchaseOrdersTable.tableColumns.orderNumber',
    createdDate: 'PurchaseOrders.purchaseOrdersTable.tableColumns.createdDate',
    updatedDate: 'PurchaseOrders.purchaseOrdersTable.tableColumns.updatedDate',
    createdBy: 'PurchaseOrders.purchaseOrdersTable.tableColumns.createdBy',
    status: 'PurchaseOrders.purchaseOrdersTable.tableColumns.status',
    total: 'PurchaseOrders.purchaseOrdersTable.tableColumns.total',
    action: 'PurchaseOrders.purchaseOrdersTable.tableColumns.action',
    selectAllAriaLabel:
      'PurchaseOrders.purchaseOrdersTable.tableColumns.selectAllAriaLabel',
    noPurchaseOrders:
      'PurchaseOrders.requireApprovalPurchaseOrders.noPurchaseOrders',
    alertHeaderApprove: 'PurchaseOrders.alertMessages.header.approve',
    alertHeaderReject: 'PurchaseOrders.alertMessages.header.reject',
    alertHeaderError: 'PurchaseOrders.alertMessages.header.error',
    alertDescriptionApprove: 'PurchaseOrders.alertMessages.description.approve',
    alertDescriptionReject: 'PurchaseOrders.alertMessages.description.reject',
    alertDescriptionError: 'PurchaseOrders.alertMessages.description.error',
  });

  const { permissions, loadingPermissions } = useCustomerRolePermissions();
  const {
    totalCount,
    loading,
    tableConfig,
    paginationConfig,
    pageSizeConfig,
    selectedOrderIds,
    handleRejectSelected,
    handleApproveSelected,
    isRequireApprovalPOsView,
    alertMessageConfig,
  } = usePurchaseOrders({
    view,
    routePurchaseOrderDetails,
    setColumns,
    setRowsData,
    initialPageSize,
    permissions,
    loadingPermissions,
    t,
  });

  return (
    <div
      className={classes(['require-approval-purchase-orders', className])}
      data-testid="require-approval-purchase-orders-container"
    >
      {withHeader ? <PurchaseOrdersHeader headerText={t.headerText} /> : null}
      <PurchaseOrdersTable
        header={
          isRequireApprovalPOsView && totalCount > 0 ? (
            <PurchaseOrdersTableActions
              loading={loading}
              selectedCount={selectedOrderIds.length}
              handleRejectSelected={handleRejectSelected}
              handleApproveSelected={handleApproveSelected}
            />
          ) : null
        }
        totalCount={totalCount}
        columns={tableConfig.columns}
        rows={tableConfig.rows}
        expandedRows={tableConfig.expandedRows}
        paginationConfig={paginationConfig}
        loading={loading}
        skeletonRowCount={skeletonRowCount}
        pageSizeConfig={pageSizeConfig}
        emptyTitle={t.noPurchaseOrders}
        alertMessageConfig={alertMessageConfig}
        withWrapper={withWrapper}
      />
    </div>
  );
};
